import { useParams, generatePath } from 'react-router-dom';
import { Alert, useTheme } from '@material-ui/core';
import { useQuery, gql } from '@apollo/client';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import OfferEdit from '../../application/offer/OfferEdit';
import Loader from '../../../ui-component/Loader';
import graphqlSchema from '../../../graphql';
import Breadcrumbs from '../../../ui-component/extended/Breadcrumbs';
import { offers } from '../../../menu-items/offers';
import {
	getBreadcrumbsNavigation,
	generateMenuItem,
} from '../../../utils/menuItem';
import { PATHS } from '../../../routes/path';

const EditPage = () => {
	const { id } = useParams<{ id: string }>();
	const { loading, error, data, refetch } = useQuery(
		gql(graphqlSchema.getOffer),
		{
			variables: { OfferId: id },
		}
	);
	const theme = useTheme();
	const offer = _get(data, 'getOffer', {});
	const offerNo = _get(offer, 'OfferNo', '');

	if (loading) return <Loader />;

	if (error && _isEmpty(offer)) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Offer not found
			</Alert>
		);
	}

	const navigation = getBreadcrumbsNavigation(
		offers,
		generateMenuItem({
			title: 'offer',
			url: generatePath(PATHS.OFFER, { id }),
			id: offerNo,
		})
	);

	return (
		<>
			<Breadcrumbs navigation={navigation} icon />
			<OfferEdit offer={offer} refetch={refetch} />
		</>
	);
};

export default EditPage;
